import React from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import TeamIllustrationSrc from "images/backtesting.png";

const Container = tw.div`relative md:-mt-24`;
const TwoColumn = tw.div`flex flex-col md:flex-row justify-between max-w-screen-xl mx-auto py-20 md:py-24 items-center`;
const Column = tw.div`w-full max-w-md mx-auto md:max-w-none md:mx-0`;
const ImageColumn = tw(Column)`md:w-6/12 flex-shrink-0 relative md:-mr-12 md:ml-12 md:-mt-16`;
const TextColumn = styled(Column)(props => [
  tw`md:w-6/12 mt-16 md:mt-0`,
  props.textOnLeft ? tw`md:mr-12 lg:mr-16 md:order-first` : tw`md:ml-12 lg:ml-16 md:order-last`
]);

const Image = styled.img(props => [
  props.imageRounded && tw`rounded`,
  props.imageBorder && tw`border`,
  props.imageShadow && tw`shadow`
]);

const TextContent = tw.div`lg:py-8 text-center md:text-left`;

const Subheading = tw(SubheadingBase)`text-center md:text-left text-gray-800`;
const Heading = tw(
  SectionHeadingH2
)`mt-4 font-black text-left text-3xl sm:text-4xl lg:text-5xl text-center md:text-left leading-tight text-gray-800`;
const Description = tw.p`mt-8 text-center md:text-left text-sm md:text-base lg:text-lg font-medium leading-relaxed text-secondary-100`;


export default ({
  subheading = "Test test test",
  heading = "Backtesting add-on",
  description = `The backtesting add-on allows for backtesting of almost all strategy parameters available in Gunbot. It can also be used to send alerts based on Gunbot strategies, to be executed by Gunbot with the TradingView add-on

  Backtesting takes place on tradingview.com, which offers a powerful engine to backtest and visualize tested trading performance. As long as tradingview.com supports the exchange you trade on, historical data for backtesting is available for almost every available coin pair at the exchange.
  
  Gunbot strategies are emulated on tradingview.com, with the exception of a few methods that cannot be replicated (like the time series analysis method).`,

  imageSrc = TeamIllustrationSrc,
  imageRounded = true,
  imageBorder = false,
  imageShadow = true,
  textOnLeft = true
}) => {
  
  return (
    <Container >
      <TwoColumn>
        <ImageColumn>
          <Image src={imageSrc} imageBorder={imageBorder} imageShadow={imageShadow} imageRounded={imageRounded} alt="Backtest every timeframe on tradingview.com"/>
        </ImageColumn>
        <TextColumn textOnLeft={textOnLeft}>
          <TextContent >
            <Subheading id='step2'>{subheading}</Subheading>
            <Heading>{heading}</Heading>
            <Description className="display-linebreak">{description}</Description>
          </TextContent>
        </TextColumn>
      </TwoColumn>
    </Container>
  );
};
