import React from "react";
import styled from "styled-components";
import tw from "twin.macro";
//eslint-disable-next-line
import { css } from "styled-components/macro"; //eslint-disable-line
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import { SectionDescription } from "components/misc/Typography.js";
import { Link } from 'react-router-dom';

const Container = tw.div`relative -mt-32 w-full`;

const ThreeColumnContainer = styled.div`
  ${tw`flex flex-col items-center md:items-stretch md:flex-row flex-wrap md:justify-center max-w-screen-lg mx-auto py-20 md:py-24`}
`;
const Subheading = tw(SubheadingBase)`mb-4 text-gray-800`;
const Heading = tw(SectionHeadingH2)`w-full text-gray-800`;
const Description = tw(SectionDescription)`w-full text-center`;

const VerticalSpacer = tw.div`mt-10 w-full`

const Column = styled.div`
  ${tw`md:w-1/2 lg:w-1/3 max-w-sm`}
`;

const Card = styled.div`
  ${tw`flex flex-col sm:flex-row items-center sm:items-start text-center sm:text-left h-full mx-4 px-2 py-8 `}
  .imageContainer {
    ${tw`border text-center rounded-full p-5 flex-shrink-0`}
    img {
      ${tw`w-6 h-6`}
    }
  }

  .textContainer {
    ${tw`sm:ml-4 mt-4 sm:mt-2 `}
  }

  .title {
    ${tw`mt-4 tracking-wide text-gray-800 font-black text-2xl leading-none`}
  }

  .description {
    ${tw`mt-1 sm:mt-4 font-medium text-secondary-100 leading-loose max-w-full`}
  }
`;

export default ({ cards = null, heading = "Why many traders choose Gunbot", subheading = "", description = `Gunbot is perfect when you want to accelerate your crypto trading. It allows you to trade profitably 24/7. 

It's likely the most privacy friendly trading bot out there, Gunbot collects no data at all about the trades users make. 
Unlike many other bots, this is not a cloud service. Your trades remain private between you and your exchange.
` }) => {

  const defaultCards = [
    {
      title: "Privacy first",
      description: `The software runs on your own computer and collects no data about your usage. 

      For security purposes, the API secrets used to connect to exchanges are only saved on your own machine.`
    },
    { 
      title: "Assist manual trading",
      description: <>Bot trading with Gunbot plays nice with manual trading on the same exchange account. 
        <br /><br />
      Want to manually add to a position Gunbot opened? No problem! Check out {<Link to="/use-cases/" title="Gunbot use cases">different ways</Link>} to use Gunbot.</>
    },
    { 
      title: "Premium Support",
      description: <>Get {<Link to="/support/" title="Gunbot support community">great support</Link>} for everything Gunbot. We'll make sure you get up and running, and don't stop there. 
        <br /><br />
      The options Gunbot offers can be pretty intimidating. We'll make sure you get an answer to every question.</>
    }
  ];

  if (!cards) cards = defaultCards;

  return (
    <Container>
      <ThreeColumnContainer>
        {subheading && <Subheading>{subheading}</Subheading>}
        <Heading>{heading}</Heading>
        {description && <Description className="display-linebreak" style={{"maxWidth" : "100%"}}>{description}</Description>}
        <VerticalSpacer />
        {cards.map((card, i) => (
          <Column key={i}>
            <Card>
              <span className="textContainer">
                <h3 className="title" tw="text-gray-800">{card.title}</h3>
                <p className="description display-linebreak">
                  {card.description}
                </p>
              </span>
            </Card>
          </Column>
        ))}
      </ThreeColumnContainer>
    </Container>
  );
};
