import React from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line
import { SectionHeading, Subheading as SubheadingBase } from "components/misc/Headings.js";
import { PrimaryButton as PrimaryButtonBase } from "components/misc/Buttons.js";
import EmailIllustrationSrc from "images/mail.png";

const Container = tw.div`relative md:-mt-12`;
const TwoColumn = tw.div`flex flex-col md:flex-row justify-between max-w-screen-xl mx-auto py-20 md:py-24`;
const Column = tw.div`w-full max-w-md mx-auto md:max-w-none md:mx-0`;
const ImageColumn = tw(Column)`md:w-5/12 flex-shrink-0 h-80 md:mr-12 md:ml-12 md:mt-12`;
const TextColumn = styled(Column)(props => [
  tw`md:w-7/12 mt-16 md:mt-0`,
  props.textOnLeft ? tw`md:mr-12 lg:mr-16 md:order-first` : tw`md:ml-12 lg:ml-16 md:order-last`
]);

const Image = styled.img(props => [
  props.imageRounded && tw`rounded`,
  props.imageBorder && tw`border`,
  props.imageShadow && tw`shadow`
]);


const TextContent = tw.div`lg:py-8 text-center md:text-left`;

const Heading = tw(SectionHeading)`mt-4 font-black text-left text-3xl sm:text-4xl lg:text-5xl text-center md:text-left leading-tight text-gray-800`;
const Description = tw.p`mt-4 text-center md:text-left text-sm md:text-base lg:text-lg font-medium leading-relaxed text-secondary-100`

const Form = tw.form`mt-8 md:mt-10 text-sm flex flex-col max-w-sm mx-auto md:mx-0`
const Input = tw.input`mt-6 first:mt-0 border-b-2 py-3 focus:outline-none font-medium transition duration-300 hocus:border-primary-500`
const Textarea = styled(Input).attrs({as: "textarea"})`
  ${tw`h-24`}
`

const SubmitButton = tw(PrimaryButtonBase)`inline-block mt-8 bg-primary-400 hover:bg-primary-500`


export default ({
  heading = "Contact form",
  description = "If you have a license problem, it helps a lot when you send all the relevant information: Gunthy token wallet, API keys, etc.",
  submitButtonText = "Send",
  formAction = "https://wt-b6e9d873fd5dfca293560032272f1f8d-0.sandbox.auth0-extend.com/mailform",
  formMethod = "get",
  textOnLeft = false,
  imageSrc = EmailIllustrationSrc,
  imageShadow = true,
  redirect = function(){
    setTimeout(function () {
      window.location.replace("https://www.gunbot.com/")
  }, 1500); 
  }
}) => {
  // The textOnLeft boolean prop can be used to display either the text on left or right side of the image.

  return (
    <Container>
      <TwoColumn>
        <ImageColumn>
          <Image src={imageSrc} imageShadow={imageShadow} alt="Contact symbol"/>
        </ImageColumn>
        <TextColumn textOnLeft={textOnLeft}>
          <TextContent>
            <Heading>{heading}</Heading>
            {description && <Description>{description}</Description>}
            <Form action={formAction} method={formMethod}>
              <Input type="email" name="from" placeholder="Your email address" required/>
              <Input type="subject" name="subject" placeholder="Subject" required/>
              <Input name="webtask_no_cache" type="hidden" value="1" />
							<Input name="webtask_no_cache" type="hidden" id="_to" value="support@gunbot.com" />
              <Textarea name="message" placeholder="Your Message Here" required/>
              <SubmitButton type="submit" onClick={() => { redirect() }}>{submitButtonText}</SubmitButton>
            </Form>
          </TextContent>
        </TextColumn>
      </TwoColumn>
    </Container>
  );
};
