import React from "react";
import tw from "twin.macro";
import { Link } from 'react-router-dom';

import {
    NavLinks
  } from "../components/headers/light.js";

const PrimaryLink = tw(Link)`lg:mx-0
px-8 py-3 rounded bg-primary-500 text-gray-100
hocus:bg-primary-700 hocus:text-gray-200 focus:shadow-outline font-semibold
border-b-0 shadow-raised lg:bg-primary-400 lg:hocus:bg-primary-500`;
const NavLink = tw(Link)`text-lg my-2 lg:text-sm lg:mx-6 lg:my-0
font-semibold tracking-wide transition duration-300
pb-1 border-b-2 border-transparent hover:border-primary-500 hocus:text-primary-500 lg:text-gray-100 lg:hocus:text-gray-300 lg:hocus:border-gray-100 `;

  
export default [
  <NavLinks key={1}>
    <NavLink to="/how-gunbot-works/" title="How Gunbot works">How it works</NavLink>
    <NavLink to="/use-cases/" title="How Gunbot works">Use cases</NavLink>
    <NavLink to="/features/" title="Gunbot feature overview">Features</NavLink>
    <NavLink to="/live-demo/" title="Live demo bot instances">Live demo</NavLink>
    <NavLink to="/support/" title="Get great support">Support</NavLink>
    <PrimaryLink to="/get-started/" title="Buy a Gunbot license">Get started</PrimaryLink>
  </NavLinks>
]

