import React from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import { ReactComponent as messageSquare } from "feather-icons/dist/icons/message-square.svg";
import { ReactComponent as mail } from "feather-icons/dist/icons/mail.svg";
import { ReactComponent as bookOpen } from "feather-icons/dist/icons/book-open.svg";
import { ReactComponent as settings } from "feather-icons/dist/icons/settings.svg";
import { ReactComponent as thumbsUp } from "feather-icons/dist/icons/thumbs-up.svg";
import { ReactComponent as users } from "feather-icons/dist/icons/users.svg";

const Container = tw.div`relative md:-mt-24 sm:mt-12`;
const TwoColumn = tw.div`flex md:flex-row justify-between max-w-screen-xl mx-auto py-20 md:py-24 items-center`;

const TextContent = tw.div`lg:py-8 text-center md:text-left `;

const Heading = tw(
  SectionHeadingH2
)`mt-4 font-black text-3xl sm:text-4xl lg:text-5xl text-center leading-tight text-gray-800`;
const Description = tw.p`mt-8 text-center text-sm md:text-base lg:text-lg font-medium leading-relaxed text-secondary-100 mb-8`;

const Features = tw.div`mx-auto md:mx-0 flex flex-col lg:flex-row max-w-xs lg:max-w-none`;
const Feature = tw.div`mt-10 lg:mt-8 flex items-center md:items-start flex-col md:mr-8 last:mr-0`;

const FeatureHeadingContainer = tw.div`flex items-center`;
const FeatureIconContainer = styled.div`
  ${tw`mx-auto inline-block border border-primary-500 text-primary-500 text-center rounded p-2 flex-shrink-0`}
  ${props => [
    props.iconRoundedFull && tw`rounded-full`,
    props.iconFilled && tw`border-0 bg-primary-500 text-gray-100`
  ]}
  svg {
    ${tw`w-5 h-5`}
  }
`;
const FeatureHeading = tw.h3`ml-3 font-bold text-xl text-gray-800`;

const FeatureDescription = tw.div`mt-4 text-center md:text-left text-gray-600 leading-relaxed`;


export default ({
  heading = "Your support channels",
  description = "Gunbot.com manages a thriving support community. We're here to help!",
  features = null,
  iconRoundedFull = true,
  iconFilled = true,
  iconContainerCss = null
}) => {
  const defaultFeatures = [
    {
      Icon: messageSquare,
      title: "Private support group",
      description: "You get direct access to some of Gunbot staff and experienced users. Our private and staffed support community on Telegram is there for you whenever you need help.",
      iconContainerCss: tw`bg-teal-300 text-teal-800`
    },
    {
      Icon: mail,
      title: "Email support",
      description: "Dont like Telegram or have an issue that requires more discretion? Of course you can also e-mail us! We will try our best to provide awesome support!",
      iconContainerCss: tw`bg-teal-300 text-teal-800`
    },
    {
      Icon: bookOpen,
      title: "Full documentation",
      description: "Gunbot is fully documented on a public wiki that is always kept up to date. You can find very detailed information about every possible setting.",
      iconContainerCss: tw`bg-teal-300 text-teal-800`
    }
  ];

  const defaultFeatures2 = [
    {
      Icon: settings,
      title: "Self service",
      description: "After the initial registration, you can self-service the API keys used for trading with Gunbot. When needed, support admins can also help you out if you need to update a key",
      iconContainerCss: tw`bg-teal-300 text-teal-800`
    },
    {
      Icon: thumbsUp,
      title: "Forum",
      description: "On the official forum users voluntarily help each other. A number of user built extensions are also available there. Announcements are often published on the forum.",
      iconContainerCss: tw`bg-teal-300 text-teal-800`
    },
    {
      Icon: users,
      title: "User community",
      description: "Gunbot has a very active user group, with loads of experienced users willing to discuss. After all, everyone started as an inexperienced user at one point.",
      iconContainerCss: tw`bg-teal-300 text-teal-800`
    }
  ];



  

  if (!features) features = defaultFeatures;

  return (
    <Container>
      <TwoColumn>
        
          <TextContent>
            <Heading>{heading}</Heading>
            <Description>{description}</Description>
            <Features>
              {features.map((feature, index) => (
                <Feature key={index}>
                  <FeatureHeadingContainer>
                    <FeatureIconContainer
                      iconFilled={iconFilled}
                      iconRoundedFull={iconRoundedFull}
                      css={feature.iconContainerCss || iconContainerCss}
                    >
                      {<feature.Icon />}
                    </FeatureIconContainer>
                    <FeatureHeading>{feature.title}</FeatureHeading>
                  </FeatureHeadingContainer>
                  <FeatureDescription>{feature.description}</FeatureDescription>
                </Feature>
              ))}
            </Features>
            <Features>
              {defaultFeatures2.map((feature, index) => (
                <Feature key={index}>
                  <FeatureHeadingContainer>
                    <FeatureIconContainer
                      iconFilled={iconFilled}
                      iconRoundedFull={iconRoundedFull}
                      css={feature.iconContainerCss || iconContainerCss}
                    >
                      {<feature.Icon />}
                    </FeatureIconContainer>
                    <FeatureHeading>{feature.title}</FeatureHeading>
                  </FeatureHeadingContainer>
                  <FeatureDescription>{feature.description}</FeatureDescription>
                </Feature>
              ))}
            </Features>
          </TextContent>
        
      </TwoColumn>
    </Container>
  );
};
