import React, { useState } from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import { SectionDescription } from "components/misc/Typography.js";
import { Container as ContainerBase, ContentWithPaddingXl as ContentBase } from "components/misc/Layouts.js";
import { ReactComponent as CheckboxIcon } from "images/checkbox-circle.svg";
import TooltipTrigger from 'react-popper-tooltip';
import { Link } from 'react-router-dom';
import 'react-popper-tooltip/dist/styles.css';
import Content from "_content/Content.js";

const Container = tw(ContainerBase)`bg-primary-900 text-gray-100 -mx-8 px-8`;
const ContentWithPaddingXl = tw(
  ContentBase
)`relative z-10 mx-auto px-0 py-10 sm:px-6 md:px-8 lg:px-12 xl:px-24 sm:py-20 flex flex-col max-w-screen-xl`;
const HeaderContainer = tw.div`mt-10 w-full flex flex-col items-center`;
const Subheading = tw(SubheadingBase)`mb-4 text-gray-100`;
const Heading = tw(SectionHeadingH2)`w-full`;
const Description = tw(SectionDescription)`w-full text-gray-300 text-center`;

const PlansContainer = tw.div`mt-16 flex flex-col items-center lg:flex-row lg:items-stretch lg:justify-between text-gray-900 font-medium`;
const Plan = styled.div`
  ${tw`w-full max-w-sm bg-white rounded-lg shadow-sm py-10 px-6 sm:px-10 lg:px-6 lg:py-10 xl:p-10 mx-3 flex flex-col justify-between mt-16 first:mt-0 lg:mt-0 shadow-raised`}
`;

const TabsControl = tw.div`flex flex-wrap bg-gray-200 px-2 py-2 rounded leading-none mt-12 xl:mt-6`;

const TabControl = styled.div`
  ${tw`cursor-pointer px-6 py-3 mt-2 sm:mt-0 sm:mr-2 last:mr-0 text-gray-600 font-medium rounded-sm transition duration-300 text-sm sm:text-base w-1/2 sm:w-auto text-center`}
  &:hover {
    ${tw`bg-gray-300 text-gray-700`}
  }
  ${props => props.active && tw`bg-primary-400! text-gray-100!`}
  }
`;

const PlanHeader = styled.div`
  .nameAndFeaturedContainer {
    ${tw`flex flex-wrap flex-col sm:flex-row justify-between items-center`}
  }
  .name {
    ${tw`text-4xl font-black text-gray-800 tracking-wider mr-3`}
  }
  .nameSmaller {
    ${tw`text-3xl font-black text-gray-800 tracking-wider mr-3`}
  }
  .featuredText {
    ${tw`text-xs font-bold px-3 rounded py-2 uppercase bg-green-300 text-green-900 leading-none mt-4 sm:mt-0 w-full sm:w-auto text-center`}
  }
  .pricingContainer {
    ${tw`mt-6 flex items-end justify-between`}
    .currentPrice {
      ${tw`text-lg font-bold leading-none`}
      .bigText {
        ${tw`text-3xl font-bold`}
      }
    }
    .oldPrice {
      ${tw`text-gray-500 text-lg line-through hidden sm:block`}
    }
  }
  .description {
    ${tw`mt-8 font-medium text-gray-700 lg:text-sm xl:text-base`}
  }
`;
const PlanFeatures = styled.ul`
  ${tw`mt-10 flex-1 border-t lg:-mx-6 -mx-6 sm:-mx-10 py-10 px-6 sm:px-10 lg:p-6 xl:-mx-10 xl:p-10`}
  .feature {
    ${tw`flex items-start mt-6 first:mt-0`}
    .icon {
      ${tw`w-6 h-6 text-green-500 flex-shrink-0`}
    }
    .text {
      ${tw`font-semibold text-primary-900 tracking-wide ml-3`}
    }
  }
`;

const Tooltip = ({ children, tooltip, hideArrow, ...props }) => (
  <TooltipTrigger
    {...props}
    tooltip={({
      arrowRef,
      tooltipRef,
      getArrowProps,
      getTooltipProps,
      placement
    }) => (
        <div
          {...getTooltipProps({
            ref: tooltipRef,
            className: 'tooltip-container display-linebreak'
          })}
        >
          {!hideArrow && (
            <div
              {...getArrowProps({
                ref: arrowRef,
                className: 'tooltip-arrow',
                'data-placement': placement
              })}
            />
          )}
          {tooltip}
        </div>
      )}
  >
    {({ getTriggerProps, triggerRef }) => (
      <span
        {...getTriggerProps({
          ref: triggerRef,
          className: 'trigger'
        })}
      >
        {children}
      </span>
    )}
  </TooltipTrigger>
);

const PlanAction = tw.div`mt-8`;

const DisplayLink = tw(Link)`block text-center text-sm font-semibold tracking-wider w-full text-gray-100 bg-primary-400 px-6 py-4 rounded hover:bg-primary-500 focus:shadow-outline focus:outline-none transition-colors duration-300`;

const WhiteBackgroundOverlay = tw.div`absolute inset-x-0 bottom-0 h-1/6 lg:h-1/3 bg-white z-0`;

export default ({
  subheading = "",
  heading = "Choose your bot",
  description = "All plans are subscription free: no recurring costs. Prices in Bitcoin.",
  plans = null,
  primaryButtonText = "I want this!",
  tabs = {
    "Gunbot plans": [],
    "Add-ons & Starter": [],
    "Upgrades": []
  }
}) => {

  if (!plans) plans = Content.plans;

  const tabsKeys = Object.keys(tabs);
  const [activeTab, setActiveTab] = useState(tabsKeys[0]);

  return (
    <Container>
      <ContentWithPaddingXl>
        <HeaderContainer>
          {subheading && <Subheading>{subheading}</Subheading>}
          <Heading>{heading}</Heading>
          {description && <Description>{description}</Description>}
          <TabsControl>
            {Object.keys(tabs).map((tabName, index) => (
              <TabControl key={index} active={activeTab === tabName} onClick={() => setActiveTab(tabName)}>
                {tabName}
              </TabControl>
            ))}
          </TabsControl>
        </HeaderContainer>
        <PlansContainer>
          {plans[activeTab].map((plan, index) => (
            <>
              {index < 3 ?
                <Plan key={index} featured={plan.featured}>

                  <PlanHeader>
                    <span className="nameAndFeaturedContainer">
                      {activeTab === tabsKeys[0] && <span className="nameSmaller">Gunbot</span>}
                      {plan.featured && <span className="featuredText">{plan.featured}</span>}
                      <span className="name">{plan.name}</span>
                    </span>
                    <div className="pricingContainer">
                      <span className="currentPrice">
                        <span className="bigText">{plan.price[0]}</span>
                        {plan.price[1]}{" "}
                      </span>
                      {plan.oldPrice && <span className="oldPrice">{plan.oldPrice}</span>}
                    </div>
                    <p className="description">{plan.description}</p>

                  </PlanHeader>
                  <PlanAction>
                    <DisplayLink title="Join Gunbot community" to={plan.url}>
                      {primaryButtonText}
                    </DisplayLink>
                  </PlanAction>
                  <PlanFeatures>
                    {plan.features.map((feature, index) => (
                      <li className="feature" key={index}>
                        <CheckboxIcon className="icon" />
                        <Tooltip placement="top" trigger="hover" className="display-linebreak" tooltip={feature[1]}>
                          <span className="text">{feature[0]}</span>
                        </Tooltip>
                      </li>
                    ))}
                  </PlanFeatures>
                </Plan>

                : null}

            </>
          ))}
        </PlansContainer>
        <PlansContainer>
          {plans[activeTab].map((plan, index) => (
            <>
              {index > 2 ?
                <Plan key={index} featured={plan.featured}>

                  <PlanHeader>
                    <span className="nameAndFeaturedContainer">
                      {activeTab === tabsKeys[0] && <span className="nameSmaller">Gunbot</span>}
                      {plan.featured && <span className="featuredText">{plan.featured}</span>}
                      <span className="name">{plan.name}</span>
                    </span>
                    <div className="pricingContainer">
                      <span className="currentPrice">
                        <span className="bigText">{plan.price[0]}</span>
                        {plan.price[1]}{" "}
                      </span>
                      {plan.oldPrice && <span className="oldPrice">{plan.oldPrice}</span>}
                    </div>
                    <p className="description">{plan.description}</p>

                  </PlanHeader>
                  <PlanAction>
                    <DisplayLink title="Join Gunbot community" to={plan.url}>
                      {primaryButtonText}
                    </DisplayLink>
                  </PlanAction>
                  <PlanFeatures>
                    {plan.features.map((feature, index) => (
                      <li className="feature" key={index}>
                        <CheckboxIcon className="icon" />
                        <Tooltip placement="top" trigger="hover" className="display-linebreak" tooltip={feature[1]}>
                          <span className="text">{feature[0]}</span>
                        </Tooltip>
                      </li>
                    ))}
                  </PlanFeatures>
                </Plan>

                : null}

            </>
          ))}
        </PlansContainer>
      </ContentWithPaddingXl>
      <WhiteBackgroundOverlay />
    </Container>
  );
};
