import React from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import TeamIllustrationSrc from "images/telegram-community.png";
import { Link } from 'react-router-dom';


const Container = tw.div`relative -mt-48`;
const TwoColumn = tw.div`flex flex-col md:flex-row justify-between max-w-screen-xl mx-auto py-20 md:py-24 items-center`;
const Column = tw.div`w-full max-w-md mx-auto md:max-w-none md:mx-0`;
const ImageColumn = tw(Column)`md:w-6/12 flex-shrink-0 relative`;
const TextColumn = styled(Column)(props => [
  tw`md:w-6/12 mt-16 md:mt-0`,
  props.textOnLeft ? tw`md:mr-12 lg:mr-16 md:order-first` : tw`md:ml-12 lg:ml-16 md:order-last`
]);

const Image = styled.img(props => [
  props.imageRounded && tw`rounded`,
  props.imageBorder && tw`border`,
  props.imageShadow && tw`shadow`
]);


const TextContent = tw.div`lg:py-8 text-center md:text-left`;

const Subheading = tw(SubheadingBase)`text-center md:text-left text-gray-800`;
const Heading = tw(
  SectionHeadingH2
)`mt-4 font-black text-left text-3xl sm:text-4xl lg:text-5xl text-center md:text-left leading-tight text-gray-800`;
const Description = tw.p`mt-8 text-center md:text-left text-sm md:text-base lg:text-lg font-medium leading-relaxed text-secondary-100`;




export default ({
  subheading = "Together we beat the market",
  heading = "An awesome community of traders",
  description = <>Gunbot has evolved massively since its first release. Nowadays the software is almost completely based on community input.
<br /><br />
The community of thousands of active traders is what makes Gunbot great. New strategies and trading features are almost always based on input from power users and tested extensively before public release. Check out the overview of all {<Link to="/features/#anchor" title="Feature overview">Gunbot features</Link>} to learn more about what you can expect. </>,
  imageSrc = TeamIllustrationSrc,
  imageRounded = true,
  imageBorder = false,
  imageShadow = true,
  textOnLeft = false,
  features = null,
}) => {
  
  if (!features) features = [];

  return (
    <Container>
      <TwoColumn>
        <ImageColumn>
          <Image src={imageSrc} imageBorder={imageBorder} imageShadow={imageShadow} imageRounded={imageRounded} alt="Very active private telegram community"/>
        </ImageColumn>
        <TextColumn textOnLeft={textOnLeft}>
          <TextContent className="display-linebreak">
            <Subheading>{subheading}</Subheading>
            <Heading>{heading}</Heading>
            <Description>{description}</Description>
          </TextContent>
        </TextColumn>
      </TwoColumn>
    </Container>
  );
};
