import React from "react";
import styled from "styled-components";
import tw from "twin.macro";
//eslint-disable-next-line
import { css } from "styled-components/macro";
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import { SectionDescriptionSmall } from "components/misc/Typography.js";




const Container = tw.div`relative bg-primary-900 -mx-8 px-8 text-gray-100`;

const ThreeColumnContainer = styled.div`
  ${tw`flex flex-col items-center md:items-stretch md:flex-row flex-wrap md:justify-center max-w-screen-lg mx-auto py-20 md:py-24`}
`;
const Heading = tw(SectionHeadingH2)`w-full`;
const Subheading = tw(SubheadingBase)`mb-4 text-gray-100`;
const Description = tw(SectionDescriptionSmall)`w-full text-left text-gray-300`;

const VerticalSpacer = tw.div`mt-10 w-full`;


export default ({
    heading = "Dollar cost averaging (DCA)",
    subheading = "Effectively handle falling markets",
    description = <>
    <p>Double up is a Gunbot method for automatically averaging down assets, also called dollar cost averaging or DCA. This allows you to reach a lower average price per unit when prices move down, making for a lower possible profitable exit price. </p>
    <br />
    <p>The behavior of DCA is highly configurable. You can set the following options:</p>
    <list>
      <li>The trigger for DCA orders: choose from (a combination of) a percentage price drop, RSI range or price crossing down the upper Bollinger Band</li>
      <li>The minimum price difference between buy orders while in DCA</li>
      <li>The number of times DCA orders are placed</li>
      <li>The amount, as a ratio compared to the amount of quote units already owned, to be bought with every DCA order</li>
    </list>
  </>,
}) => {

    return (
        <Container>
            <ThreeColumnContainer>
                {subheading && <Subheading>{subheading}</Subheading>}
                <Heading id='supportedExchanges'>{heading}</Heading>
                {description && <Description className="display-linebreak" style={{ maxWidth: "100%" }}>{description}</Description>}
                <VerticalSpacer />
                
            </ThreeColumnContainer>
        </Container>
    );
};
