import React from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import TeamIllustrationSrc from "images/trailing.png";

const Container = tw.div`relative -mt-24`;
const TwoColumn = tw.div`flex flex-col md:flex-row justify-between max-w-screen-xl mx-auto py-20 md:py-24 items-center`;
const Column = tw.div`w-full max-w-md mx-auto md:max-w-none md:mx-0`;
const ImageColumn = tw(Column)`md:w-6/12 flex-shrink-0 relative md:-mt-8 md:-mr-12 md:ml-12`;
const TextColumn = styled(Column)(props => [
  tw`md:w-6/12 mt-16 md:mt-0`,
  props.textOnLeft ? tw`md:mr-12 lg:mr-16 md:order-first` : tw`md:ml-12 lg:ml-16 md:order-last`
]);

const Image = styled.img(props => [
  props.imageRounded && tw`rounded`,
  props.imageBorder && tw`border`,
  props.imageShadow && tw`shadow`
]);

const TextContent = tw.div`lg:py-8 text-center md:text-left`;
const list = tw.ul`list-disc`;

const Subheading = tw(SubheadingBase)`text-center md:text-left text-gray-800`;
const Heading = tw(
  SectionHeadingH2
)`mt-4 font-black text-left text-3xl sm:text-4xl lg:text-5xl text-center md:text-left leading-tight text-gray-800`;
const Description = tw.p`mt-8 text-center md:text-left text-sm md:text-base lg:text-lg font-medium leading-relaxed text-secondary-100`;


export default ({
  subheading = "Get the best out of your trades:",
  heading = "Additional trailing",
  description = <>
    <p>Price trailing can be used for most types of orders, to reach optimal entry- or exit points.</p>
    <br />
    <p>For example: you have set up a buy method based on buying when prices reach the lower Bollinger band. Without additional trailing a buy order is placed as soon as price hits the lower band. With trailing, Gunbot follows prices as they move down even further, and only buy once the trailing stop is hit while price is still at or below the lower Bollinger Band. </p>
    <br />
    <p>You can also use Gunbot for sell trailing for assets you bought manually.</p>
    <br />
    <p>Types of orders that allow for additional trailing:</p>
    <list>
      <li>Regular buy orders </li>
      <li>Regular sell orders </li>
      <li>DCA buy orders </li>
      <li>Reversal trading buy orders </li>
      <li>Reversal trading sell orders </li>
      <li>Futures trading close orders </li>
    </list>
  </>,

  imageSrc = TeamIllustrationSrc,
  imageRounded = true,
  imageBorder = false,
  imageShadow = true,
  textOnLeft = true
}) => {

  return (
    <Container >
      <TwoColumn>
        <ImageColumn>
          <Image src={imageSrc} imageBorder={imageBorder} imageShadow={imageShadow} imageRounded={imageRounded} alt="get better trades with trailing"/>
        </ImageColumn>
        <TextColumn textOnLeft={textOnLeft}>
          <TextContent >
            <Subheading id='step2'>{subheading}</Subheading>
            <Heading>{heading}</Heading>
            <Description className="display-linebreak">{description}</Description>
          </TextContent>
        </TextColumn>
      </TwoColumn>
    </Container>
  );
};
