import React from "react";
import styled from "styled-components";
import tw from "twin.macro";
//eslint-disable-next-line
import { css } from "styled-components/macro";
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import { SectionDescriptionSmall } from "components/misc/Typography.js";




const Container = tw.div`relative -mx-8 px-8 text-gray-100`;

const ThreeColumnContainer = styled.div`
  ${tw`flex flex-col items-center md:items-stretch md:flex-row flex-wrap md:justify-center max-w-screen-lg mx-auto py-20 md:py-24`}
`;
const Heading = tw(SectionHeadingH2)`w-full text-gray-800`;
const Subheading = tw(SubheadingBase)`mb-4 text-gray-100 text-gray-800`;
const Description = tw(SectionDescriptionSmall)`w-full text-left text-secondary-100`;

const VerticalSpacer = tw.div`mt-10 w-full`;


export default ({
    heading = "Tips for using the demo",
    subheading = "Live demo",
    description = `You cannot make changes to the demo system. They are live trading bot instances, but only meant for demonstration purposes.

    To see data for all active trading pairs, go to the dashboard where you can get detailed info about all current trading targets.
    Click "PNL analysis" on the chart to get detailed profit and loss overviews, for all and for individual trading pairs.
    
    In case the bot looks like it has stopped, push the "Start Core" button on the settings page. You'll soon see data appearing on the dashboard and chart.`,
}) => {

    return (
        <Container>
            <ThreeColumnContainer>
                {subheading && <Subheading>{subheading}</Subheading>}
                <Heading id='supportedExchanges'>{heading}</Heading>
                {description && <Description className="display-linebreak" style={{ maxWidth: "100%" }}>{description}</Description>}
                <VerticalSpacer />
                
            </ThreeColumnContainer>
        </Container>
    );
};
