import React from "react";
import styled from "styled-components";
import tw from "twin.macro";
//eslint-disable-next-line
import { css } from "styled-components/macro";
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import { SectionDescriptionSmall } from "components/misc/Typography.js";




const Container = tw.div`relative bg-primary-900 -mx-8 px-8 text-gray-100 -mt-24`;

const ThreeColumnContainer = styled.div`
  ${tw`flex flex-col items-center md:items-stretch md:flex-row flex-wrap md:justify-center max-w-screen-lg mx-auto py-20 md:py-24`}
`;
const Heading = tw(SectionHeadingH2)`w-full`;
const Subheading = tw(SubheadingBase)`mb-4 text-gray-100`;
const Description = tw(SectionDescriptionSmall)`w-full text-left text-gray-300`;

const VerticalSpacer = tw.div`mt-10 w-full`;


export default ({
    heading = "Reversal Trading",
    subheading = "Accumulate",
    description = `Gunbot can automatically accumulate quote currency when prices move down, without investing more than the initial buy order. This feature is called reversal trading and can help bring down the break-even point for your investment.

    With reversal trading, Gunbot will sell quote units owned at a configurable percentage price drop, then buy back more quote units using only the funds acquired from the previous sell order. This process can keep repeating while prices move down further, or even when the market goes sideways after an initial drop.
    
    While accumulating, Gunbot keeps track of the trading fees paid during the process. As soon price hits the break-even point, it will continue with regular trading.`,
}) => {

    return (
        <Container>
            <ThreeColumnContainer>
                {subheading && <Subheading>{subheading}</Subheading>}
                <Heading id='supportedExchanges'>{heading}</Heading>
                {description && <Description className="display-linebreak" style={{ maxWidth: "100%" }}>{description}</Description>}
                <VerticalSpacer />
                
            </ThreeColumnContainer>
        </Container>
    );
};
