import React from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import TeamIllustrationSrc from "images/exit-manager.png";

const Container = tw.div`relative`;
const TwoColumn = tw.div`flex flex-col md:flex-row justify-between max-w-screen-xl mx-auto py-20 md:py-24 items-center`;
const Column = tw.div`w-full max-w-md mx-auto md:max-w-none md:mx-0`;
const ImageColumn = tw(Column)`md:w-6/12 flex-shrink-0 relative md:-mt-8 md:-ml-12 md:mr-12`;
const TextColumn = styled(Column)(props => [
  tw`md:w-6/12 mt-16 md:mt-0`,
  props.textOnLeft ? tw`md:mr-12 lg:mr-16 md:order-first` : tw`md:ml-12 lg:ml-16 md:order-last`
]);

const Image = styled.img(props => [
  props.imageRounded && tw`rounded`,
  props.imageBorder && tw`border`,
  props.imageShadow && tw`shadow`
]);

const TextContent = tw.div`lg:py-8 text-center md:text-left`;

const Subheading = tw(SubheadingBase)`text-center md:text-left text-gray-800`;
const Heading = tw(
  SectionHeadingH2
)`mt-4 font-black text-left text-3xl sm:text-4xl lg:text-5xl text-center md:text-left leading-tight text-gray-800`;
const Description = tw.p`mt-8 text-center md:text-left text-sm md:text-base lg:text-lg font-medium leading-relaxed text-secondary-100`;


export default ({
  subheading = "Buy manually, sell like a robot ",
  heading = "Gunbot as exit manager",
  description = `Are you the type of trader that likes manual trading? With Gunbot you can speed things up by buying manually and letting the bot take care of optimal exits for your trades.

  Use powerful sell methods to be sure to sell close to the next peak. With "stepgain" you can take advantage of the built-in trend watcher, Gunbot would sell when price is above a minimum acceptable gain and the trend changes direction. With "tssl" you can set a trailing range to handle selling purely based on price action.
  
  Because Gunbot always pulls trade data from the exchange, it's perfectly able to mix with manual trading. In case multiple buys happened, it will automatically work out the average price per unit including trading fees.`,
  imageSrc = TeamIllustrationSrc,
  imageRounded = true,
  imageBorder = false,
  imageShadow = true,
  textOnLeft = false
}) => {
  
  return (
    <Container >
      <TwoColumn>
        <ImageColumn>
          <Image src={imageSrc} imageBorder={imageBorder} imageShadow={imageShadow} imageRounded={imageRounded} alt="Use a bot for closing positions"/>
        </ImageColumn>
        <TextColumn textOnLeft={textOnLeft}>
          <TextContent >
            <Subheading id='step2'>{subheading}</Subheading>
            <Heading>{heading}</Heading>
            <Description className="display-linebreak">{description}</Description>
          </TextContent>
        </TextColumn>
      </TwoColumn>
    </Container>
  );
};
