import React, { useState } from "react";
import { motion } from "framer-motion";
import styled from "styled-components";
import tw from "twin.macro";
import { css } from "styled-components/macro"; //eslint-disable-line
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import { SectionDescription } from "components/misc/Typography.js";
import { Container, ContentWithPaddingXl } from "components/misc/Layouts.js";
import { ReactComponent as ChevronDownIcon } from "feather-icons/dist/icons/chevron-down.svg";
import JSONInput from 'react-json-editor-ajrm';
import locale    from 'react-json-editor-ajrm/locale/en';
import configExamples from "_content/ConfigExamples.js";


const Description = tw(SectionDescription)`w-full text-center`;
const Subheading = tw(SubheadingBase)`text-center text-gray-800`;
const Heading = tw(
  SectionHeadingH2
)`mt-4 font-black text-left text-3xl sm:text-4xl lg:text-5xl text-center leading-tight text-gray-800`;

const Column = tw.div`flex flex-col items-center -mt-24`;
const HeaderContent = tw.div``;

const FAQSContainer = tw.dl`mt-8 lg:w-9/12 relative`;
const FAQ = tw.div`cursor-pointer select-none mt-5 px-8 sm:px-10 py-5 sm:py-4 rounded-lg text-gray-800 hover:text-gray-900 bg-gray-200 hover:bg-gray-300 transition duration-300`;
const Question = tw.dt`flex justify-between items-center`;
const QuestionText = tw.span`text-lg lg:text-xl font-semibold`;
const QuestionToggleIcon = motion.custom(styled.span`
  ${tw`ml-2 transition duration-300`}
  svg {
    ${tw`w-6 h-6`}
  }
`);
const Answer = motion.custom(tw.dd`pointer-events-none text-sm sm:text-base leading-relaxed`);

export default ({
  subheading = "JSON config files",
  heading = "Developer friendly",
  description = `Don't like graphical user interfaces? You can also control Gunbot by editing a JSON config file. Every time changes are written to the config file, changed settings are loaded and applied seamlessly.
  
  Have a look at a few examples showing how easy it is to configure Gunbot without GUI.`,
  faqs = [
    {
      question: "Config example: trading pairs",
      answer:
        <>
        <JSONInput 
              id='a_unique_id'
              placeholder={configExamples.pairs}
              locale={locale}
              viewOnly={true}
              confirmGood={false}
              height='auto'
              width='100%'
            />
        </>
    },
    {
      question: "Config example: strategy",
      answer:
        <>
        <JSONInput
              id='a_unique_id'
              placeholder={configExamples.strategies}
              locale={locale}
              viewOnly={true}
              confirmGood={false}
              height='auto'
              width='100%'
            />
        </>
    },
    {
      question: "Config example: automated config changes with AutoConfig",
      answer:
        <>
        <JSONInput
              id='a_unique_id'
              placeholder={configExamples.autoconfig}
              locale={locale}
              viewOnly={true}
              confirmGood={false}
              height='auto'
              width='100%'
            />
        </>
    },
    
  ]
}) => {
  const [activeQuestionIndex, setActiveQuestionIndex] = useState(null);

  const toggleQuestion = questionIndex => {
    if (activeQuestionIndex === questionIndex) setActiveQuestionIndex(null);
    else setActiveQuestionIndex(questionIndex);
  };

  return (
    <Container>
      <ContentWithPaddingXl>
        <Column>
          <HeaderContent>
            {subheading && <Subheading>{subheading}</Subheading>}
            <Heading>{heading}</Heading>
            {description && <Description className="display-linebreak">{description}</Description>}
          </HeaderContent>
          <FAQSContainer>
            {faqs.map((faq, index) => (
              <FAQ
                key={index}
                onClick={() => {
                  toggleQuestion(index);
                }}
                className="group"
              >
                <Question>
                  <QuestionText>{faq.question}</QuestionText>
                  <QuestionToggleIcon
                    variants={{
                      collapsed: { rotate: 0 },
                      open: { rotate: -180 }
                    }}
                    initial="collapsed"
                    animate={activeQuestionIndex === index ? "open" : "collapsed"}
                    transition={{ duration: 0.02, ease: [0.04, 0.62, 0.23, 0.98] }}
                  >
                    <ChevronDownIcon />
                  </QuestionToggleIcon>
                </Question>
                <Answer
                  variants={{
                    open: { opacity: 0.95, height: "auto", marginTop: "16px" },
                    collapsed: { opacity: 0, height: 0, marginTop: "0px" }
                  }}
                  initial="collapsed"
                  animate={activeQuestionIndex === index ? "open" : "collapsed"}
                  transition={{ duration: 0.3, ease: [0.04, 0.62, 0.23, 0.98] }}
                >
                  {faq.answer}
                </Answer>
              </FAQ>
            ))}
          </FAQSContainer>
        </Column>
      </ContentWithPaddingXl>
    </Container>
  );
};
