import React from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import TeamIllustrationSrc from "images/pnl-large.png";

const Container = tw.div`relative`;
const TwoColumn = tw.div`flex flex-col md:flex-row justify-between max-w-screen-xl mx-auto py-20 md:py-24 items-center`;
const Column = tw.div`w-full max-w-md mx-auto md:max-w-none md:mx-0`;
const ImageColumn = tw(Column)`md:w-6/12 flex-shrink-0 relative md:-mt-8 md:-ml-12 md:mr-12`;
const TextColumn = styled(Column)(props => [
  tw`md:w-6/12 mt-16 md:mt-0`,
  props.textOnLeft ? tw`md:mr-12 lg:mr-16 md:order-first` : tw`md:ml-12 lg:ml-16 md:order-last`
]);

const Image = styled.img(props => [
  props.imageRounded && tw`rounded`,
  props.imageBorder && tw`border`,
  props.imageShadow && tw`shadow`
]);

const TextContent = tw.div`lg:py-8 text-center md:text-left`;

const Subheading = tw(SubheadingBase)`text-center md:text-left text-gray-800`;
const Heading = tw(
  SectionHeadingH2
)`mt-4 font-black text-left text-3xl sm:text-4xl lg:text-5xl text-center md:text-left leading-tight text-gray-800`;
const Description = tw.p`mt-8 text-center md:text-left text-sm md:text-base lg:text-lg font-medium leading-relaxed text-secondary-100`;


export default ({
  subheading = "Easy to use",
  heading = "Powerful browser interface",
  description = `Gunbot comes with a built-in browser interface. You can safely access the interface on your local machine, or open up access from the internet. The interface is optimized for mobile devices, supports two factor authentication and can be served via https.

  Using the interface, you can easily manage your trading strategies, assign them to trading pairs and make changes on the fly. The dashboard shows all recent trades and wallet balances.
  
  Additionally you can view detailed charts built with the data Gunbot collects from the exchange, trades are shown in these charts. This visualization of your trades makes it easy to optimize your trading strategies.
  
  In case you dont want to wait for the bot to place an order, you can place manual orders directly in the Gunbot interface. This also allows for manually averaging down, as Gunbot works with the average price paid for assets  regardless if Gunbot placed an order of you did so manually.`,

  imageSrc = TeamIllustrationSrc,
  imageRounded = true,
  imageBorder = false,
  imageShadow = true,
  textOnLeft = false
}) => {
  
  return (
    <Container >
      <TwoColumn>
        <ImageColumn>
          <Image src={imageSrc} imageBorder={imageBorder} imageShadow={imageShadow} imageRounded={imageRounded} alt="Profit and loss statistics"/>
        </ImageColumn>
        <TextColumn textOnLeft={textOnLeft}>
          <TextContent >
            <Subheading id='step2'>{subheading}</Subheading>
            <Heading>{heading}</Heading>
            <Description className="display-linebreak">{description}</Description>
          </TextContent>
        </TextColumn>
      </TwoColumn>
    </Container>
  );
};
