import React from "react";
import styled from "styled-components";
import tw from "twin.macro";
//eslint-disable-next-line
import { css } from "styled-components/macro";
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import { SectionDescriptionSmall } from "components/misc/Typography.js";




const Container = tw.div`relative bg-primary-900 -mx-8 px-8 text-gray-100 -mt-24`;

const ThreeColumnContainer = styled.div`
  ${tw`flex flex-col items-center md:items-stretch md:flex-row flex-wrap md:justify-center max-w-screen-lg mx-auto py-20 md:py-24`}
`;
const Heading = tw(SectionHeadingH2)`w-full`;
const Subheading = tw(SubheadingBase)`mb-4 text-gray-100`;
const Description = tw(SectionDescriptionSmall)`w-full text-left text-gray-300`;

const VerticalSpacer = tw.div`mt-10 w-full`;


export default ({
    heading = "Balance options",
    subheading = "Fine grained control",
    description = `You can set per pair limits for the amount of base currency to invest per trade. The trading limit can be set as an absolute amount, or as a percentage of available base currency.

    When selling, you can choose to sell all quote currency, or keep a number of quote units. Gunbot can be set to ignore dust: small holdings below the minimum trade size of the exchange.
    
    A funds reserve feature is available to keep an absolute amount of base currency reserved at all times.`,
}) => {

    return (
        <Container>
            <ThreeColumnContainer>
                {subheading && <Subheading>{subheading}</Subheading>}
                <Heading id='supportedExchanges'>{heading}</Heading>
                {description && <Description className="display-linebreak" style={{ maxWidth: "100%" }}>{description}</Description>}
                <VerticalSpacer />
                
            </ThreeColumnContainer>
        </Container>
    );
};
