import React, { useEffect, useState } from 'react';
import AnimationRevealPage from "helpers/AnimationRevealPage.js";
import Hero from "components/custom/seoLandingPage/Hero.js";
import Block1 from "components/custom/seoLandingPage/Block1.js";
import Block2 from "components/custom/seoLandingPage/Block2.js";
import Cta from "components/custom/seoLandingPage/Cta.js";
import Footer from "components/custom/Footer.js";
import Exchanges from "components/custom/features/Exchanges.js";
import { useLocation } from 'react-router-dom'

import { Helmet } from 'react-helmet';

import Content from "_content/SeoContent.js";

const ldJson1 = {
  "@context": "https://schema.org",
  "@type": "Organization",
  "url": "https://www.gunbot.com",
  "logo": "https://www.gunbot.com/logo192.png"
};
const ldJson2 = {
  "@context": "http://schema.org",
  "@type": "WebPage",
  "name": "Gunbot.com",
  "description": "Gunbot is the leading crypto trading bot for popular exchanges like Binance, Coinbase Pro, Bitmex and more.",
  "publisher": {
    "@type": "ProfilePage",
    "name": "Gunbot.com"
  }
};
const ldJson3 = {
  "@context": "https://schema.org",
  "@type": "Organization",
  "name": "Gunbot.com",
  "url": "https://www.gunbot.com",
  "sameAs": [
    "https://medium.com/awesomegunbot"
  ]
};

export default () => {

  const [urlPath, setUrlPath] = useState("");
  let location = useLocation()

  useEffect(() => {
    setUrlPath(location.pathname)
  }, [location]);

  return (
    <>
      {urlPath !== "" && Object.keys(Content.pages).includes(urlPath) ?
        <>
          <Helmet>
            <title>{Content.pages[urlPath].title}</title>
            <meta name="description" content={Content.pages[urlPath].description} />
            <meta property="title" content={Content.pages[urlPath].title} />
            <link rel="canonical" href={'https://www.gunbot.com' + urlPath} />
            <meta property="og:locale" content="en_US" />
            <meta property="og:type" content="website" />
            <meta property="og:hashtag" content="#gunbot" />
            <meta property="og:title" content={Content.pages[urlPath].title} />
            <meta property="og:url" content={'https://www.gunbot.com' + urlPath} />
            <meta property="og:image" content="https://www.gunbot.com/preview.png" />
            <meta property="og:site_name" content="Gunbot.com" />
            <meta property="og:description" content={Content.pages[urlPath].description} />
            <script type="application/ld+json">
              {JSON.stringify(ldJson1)}
            </script>
            <script type="application/ld+json">
              {JSON.stringify(ldJson2)}
            </script>
            <script type="application/ld+json">
              {JSON.stringify(ldJson3)}
            </script>
          </Helmet>
        </>
        :
        <>
          <Helmet>
            <title>Loading...</title>
            <meta name="description" content="Loading..." />
          </Helmet>
        </>
      }


      {/*
         <title>{Content.pages[urlPath].title}</title>
          <meta name="description" content={Content.pages[urlPath].description} />
          */}

      <AnimationRevealPage>
        <Hero />
        {urlPath == "/exchanges/" ?
          <>
            <Exchanges />
          </>
          :
          null
        }
        <Block1 />
        <Block2 />
        <Cta />
        <Footer />
      </AnimationRevealPage>
    </>
  );
}
