import React from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import TeamIllustrationSrc from "images/indicators-large.png";

const Container = tw.div`relative -mt-24`;
const TwoColumn = tw.div`flex flex-col md:flex-row justify-between max-w-screen-xl mx-auto py-20 md:py-24 items-center`;
const Column = tw.div`w-full max-w-md mx-auto md:max-w-none md:mx-0`;
const ImageColumn = tw(Column)`md:w-6/12 flex-shrink-0 relative md:-mt-16 md:-ml-12 md:mr-12`;
const TextColumn = styled(Column)(props => [
  tw`md:w-6/12 mt-16 md:mt-0`,
  props.textOnLeft ? tw`md:mr-12 lg:mr-16 md:order-first` : tw`md:ml-12 lg:ml-16 md:order-last`
]);

const Image = styled.img(props => [
  props.imageRounded && tw`rounded`,
  props.imageBorder && tw`border`,
  props.imageShadow && tw`shadow`
]);

const TextContent = tw.div`lg:py-8 text-center md:text-left`;
const list = tw.ul`list-disc`;

const Subheading = tw(SubheadingBase)`text-center md:text-left text-gray-800`;
const Heading = tw(
  SectionHeadingH2
)`mt-4 font-black text-left text-3xl sm:text-4xl lg:text-5xl text-center md:text-left leading-tight text-gray-800`;
const Description = tw.p`mt-8 text-center md:text-left text-sm md:text-base lg:text-lg font-medium leading-relaxed text-secondary-100`;


export default ({
  subheading = "Combine freely",
  heading = "Confirming indicators",
  description = <>
    <p>To refine your trading strategy, you can configure confirming indicators which restrict your buy/sell method to trading only when certain indicators are within a range you configure. </p>
    <br />
    <p>For example: only allow buy orders when RSI is 30 or lower. The period settings for every indicator are fully configurable.</p>
    <br />
    <p>Available confirming indicators, natively available in strategies:</p>
    <list>
      <li>ADX</li>
      <li>EMA</li>
      <li>EMA spread</li>
      <li>MFI</li>
      <li>RSI</li>
      <li>EMA spread</li>
      <li>Stochastic</li>
      <li>StochRSI</li>
      <li>Optional: extend your strategy with many more <a href="https://tulipindicators.org/list" title="Gunbot support the full Tulip indicator library" target="_blank" rel="noopener noreferrer">indicator</a> combinations using AutoConfig</li>
    </list>
  </>,

  imageSrc = TeamIllustrationSrc,
  imageRounded = true,
  imageBorder = false,
  imageShadow = true,
  textOnLeft = false
}) => {

  return (
    <Container >
      <TwoColumn>
        <ImageColumn>
          <Image src={imageSrc} imageBorder={imageBorder} imageShadow={imageShadow} imageRounded={imageRounded} alt="Industry standard trading indicators"/>
        </ImageColumn>
        <TextColumn textOnLeft={textOnLeft}>
          <TextContent >
            <Subheading id='step2'>{subheading}</Subheading>
            <Heading>{heading}</Heading>
            <Description className="display-linebreak">{description}</Description>
          </TextContent>
        </TextColumn>
      </TwoColumn>
    </Container>
  );
};
