import React, { useEffect, useState } from 'react';
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line
import Navigation from "_content/Navigation.js";
import { Link, useLocation } from 'react-router-dom';
import Content from "_content/SeoContent.js";

import HeaderBase, {
  LogoLink as LogoLinkBase,
} from "../../headers/light.js";

import ResponsiveVideoEmbed from "helpers/ResponsiveVideoEmbed.js";

import { Container as ContainerBase, ContentWithVerticalPadding, Content2Xl } from "components/misc/Layouts.js";
import { SectionHeading } from "components/misc/Headings.js";
import { SectionDescription } from "components/misc/Typography.js";
import logoImageSrc from "images/logo-light.svg";
import headerImage from "images/gunbot-interface.png";

const PrimaryBackgroundContainer = tw.div`-mx-8 px-8 bg-primary-900 text-gray-100`;
const Header = tw(HeaderBase)`max-w-none -mt-8 py-8 -mx-8 px-8`;
const LogoLink = tw(LogoLinkBase)`text-gray-100`;

const Container = tw(ContainerBase)`-mt-12`;
const Row = tw.div`flex items-center flex-col lg:flex-row`;
const Column = tw.div`lg:w-1/2`;
const TextColumn = tw.div`text-center lg:text-left`;
const IllustrationColumn = tw(Column)`mt-16 lg:mt-0 lg:ml-16`;
const Heading = tw(SectionHeading)`max-w-3xl lg:max-w-4xl lg:text-left leading-tight`;
const Description = tw(SectionDescription)`mt-4 max-w-2xl text-gray-100 lg:text-base mx-auto lg:mx-0`;


const Actions = styled.div`
  ${tw`mb-8 lg:mb-0 mt-6`}
  .action {
    ${tw`text-center inline-block w-full sm:w-48 py-4 font-semibold tracking-wide rounded hocus:outline-none focus:shadow-outline transition duration-300`}
  }
  .primaryAction {
    ${tw`bg-primary-400 text-gray-100 hover:bg-primary-500`}
  }
  .secondaryAction {
    ${tw`mt-4 sm:mt-0 sm:ml-4 bg-gray-300 text-gray-700 hover:bg-gray-400 hover:text-gray-800`}
  }
`;

const StyledResponsiveVideoEmbed = styled(ResponsiveVideoEmbed)`
  padding-bottom: 56.25% !important;
  padding-top: 0px !important;
  ${tw`rounded`}
  iframe {
    ${tw`rounded bg-black shadow-xl`}
  }
`;

const Notification = tw.span`inline-block my-4 pl-3 py-1 text-gray-100 border-l-4 border-blue-500 font-medium text-sm`;

export default ({
  primaryActionText = "Get license",
  primaryActionUrl = "/get-started/",
  secondaryActionText = "How Gunbot works",
  secondaryActionUrl = "/how-gunbot-works/",
  imageSrc = headerImage,

}) => {
  const logoLink = (
    <LogoLink to="/">
      <img src={logoImageSrc} alt="Gunbot logo" width="40px" height="40px"/>
      Gunbot
    </LogoLink>
  );
  const [urlPath, setUrlPath] = useState("");
  let location = useLocation()

  useEffect(() => {
    if (Object.keys(Content.pages).includes(location.pathname)){
      setUrlPath(location.pathname)
    }
  }, [location]);

  const heading = Content.pages[urlPath].heading || "";
  const notification = Content.pages[urlPath].notification || "";
  const heroDescription = Content.pages[urlPath].heroDescription || "";

  return (
    <PrimaryBackgroundContainer>
      <Content2Xl>
        <Header logoLink={logoLink} links={Navigation} />
        <Container>
          <ContentWithVerticalPadding>
            <Row>
              <TextColumn>
                <Notification>{notification}</Notification>
                <Heading>{heading}</Heading>
                <Description>{heroDescription}</Description>
                <br />
                <Actions>
                  <Link to={primaryActionUrl} title="Get Gunbot license" className="action primaryAction">
                    {primaryActionText}
                  </Link>
                  <Link to={secondaryActionUrl} title="How Gunbot works" className="action secondaryAction">
                    {secondaryActionText}
                  </Link>
                </Actions>
              </TextColumn>
              <IllustrationColumn style={{ width: '75%' }}>
                <StyledResponsiveVideoEmbed
                  url='https://www.youtube-nocookie.com/embed/-0F3ECSQu1c?controls=0" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture'
                  background="transparent"
                />
              </IllustrationColumn>
            </Row>
          </ContentWithVerticalPadding>
        </Container>
      </Content2Xl>
    </PrimaryBackgroundContainer>
  );
};
