import React from "react";
import AnimationRevealPage from "helpers/AnimationRevealPage.js";
import Hero from "components/custom/useCases/Hero.js";
import ExitManager from "components/custom/useCases/exitManager.js";
import AutomatedTrading from "components/custom/useCases/automatedTrading.js";
import BagManager from "components/custom/useCases/bagManager.js";
import TradingView from "components/custom/useCases/tradingView.js";
import Cta from "components/custom/useCases/Cta.js";
import Footer from "components/custom/Footer.js";
import { Helmet } from 'react-helmet';


import Content from "_content/Content.js";
const locationString = "/use-cases/"

const ldJson1 = {
  "@context": "https://schema.org",
  "@type": "Organization",
  "url": "https://www.gunbot.com",
  "logo": "https://www.gunbot.com/logo192.png"
};
const ldJson2 = {
  "@context": "http://schema.org",
  "@type": "WebPage",
  "name": "Gunbot.com",
  "description": "Gunbot is the leading crypto trading bot for popular exchanges like Binance, Coinbase Pro, Bitmex and more.",
  "publisher": {
    "@type": "ProfilePage",
    "name": "Gunbot.com"
  }
};
const ldJson3 = {
  "@context": "https://schema.org",
  "@type": "Organization",
  "name": "Gunbot.com",
  "url": "https://www.gunbot.com",
  "sameAs": [
    "https://medium.com/awesomegunbot"
  ]
};

export default () => {
  return (
    <>
      <Helmet>
        <title>{Content.SEO[locationString].title}</title>
        <meta name="description" content={Content.SEO[locationString].description} />
        <meta property="title" content={Content.SEO[locationString].title} />
        <link rel="canonical" href={'https://www.gunbot.com' + locationString} />
        <meta property="og:locale" content="en_US" />
        <meta property="og:type" content="website" />
        <meta property="og:hashtag" content="#gunbot" />
        <meta property="og:title" content={Content.SEO[locationString].title} />
        <meta property="og:url" content={'https://www.gunbot.com' + locationString} />
        <meta property="og:image" content="https://www.gunbot.com/preview.png" />
        <meta property="og:site_name" content="Gunbot.com" />
        <meta property="og:description" content={Content.SEO[locationString].description} />
        <script type="application/ld+json">
          {JSON.stringify(ldJson1)}
        </script>
        <script type="application/ld+json">
          {JSON.stringify(ldJson2)}
        </script>
        <script type="application/ld+json">
          {JSON.stringify(ldJson3)}
        </script>
      </Helmet>
      <AnimationRevealPage>
        <Hero />
        <ExitManager />
        <AutomatedTrading />
        <BagManager />
        <TradingView />
        <Cta />
        <Footer />
      </AnimationRevealPage>
    </>
  );
}
