import React from "react";
import styled from "styled-components";
import tw from "twin.macro";
//eslint-disable-next-line
import { css } from "styled-components/macro";
import { SectionHeadingH2, Subheading as SubheadingBase } from "components/misc/Headings.js";
import { SectionDescriptionSmall } from "components/misc/Typography.js";




const Container = tw.div`relative bg-primary-900 -mx-8 px-8 text-gray-100`;

const ThreeColumnContainer = styled.div`
  ${tw`flex flex-col items-center md:items-stretch md:flex-row flex-wrap md:justify-center max-w-screen-lg mx-auto py-20 md:py-24`}
`;
const Heading = tw(SectionHeadingH2)`w-full`;
const Subheading = tw(SubheadingBase)`mb-4 text-gray-100`;
const Description = tw(SectionDescriptionSmall)`w-full text-left text-gray-300`;

const VerticalSpacer = tw.div`mt-10 w-full`;


export default ({
    heading = "Tested trading strategies",
    subheading = "Use presets or individual strategies",
    description = `A strategy in Gunbot is a set of rules that define how it trades. Every trading pair can be assigned a single strategy. Different pairs can use different trading strategies.

    The main components of a strategy are buy and sell methods, which define when the bot is allowed to place orders. Strategies can be assigned to one or more trading pairs. Gunbot comes with many free strategy presets, these are ready to use after making minimal changes - like configuring how much it is allowed to spend. You can create an unlimited number of custom strategies.
    
    Strategies are specially adapted for spot and futures trading. On futures, you can choose between mean reversion or trend following variants of individual strategies. With trailing you can maximize profit from any position.
    
    Every strategy can use a number of protections, like setting custom gain and stop limit values, limiting the number of sell orders before trading is halted, prevent buy orders above the last sell rate to protect against buying in surging markets.`
}) => {

    return (
        <Container>
            <ThreeColumnContainer>
                {subheading && <Subheading>{subheading}</Subheading>}
                <Heading id='supportedExchanges'>{heading}</Heading>
                {description && <Description className="display-linebreak" style={{ maxWidth: "100%" }}>{description}</Description>}
                <VerticalSpacer />
                
            </ThreeColumnContainer>
        </Container>
    );
};
