import React, { useState } from "react";
import { motion } from "framer-motion";
import tw from "twin.macro";
import styled, { css } from "styled-components/macro"; //eslint-disable-line
import { Container, ContentWithPaddingXl } from "components/misc/Layouts.js";
import { SectionHeading, Subheading as SubheadingBase } from "components/misc/Headings.js";
import { SectionDescriptionSmall } from "components/misc/Typography.js";
import { ReactComponent as ChevronDownIcon } from "feather-icons/dist/icons/chevron-down.svg";

const PrimaryBackgroundContainer = tw(Container)`-mx-8 px-8 bg-primary-900 text-gray-100 -mt-24 mb-24`;

const HeadingContainer = tw.div``;
const Subheading = tw(SubheadingBase)`text-center text-gray-100 mb-4`;
const Heading = tw(SectionHeading)``;
const Description = tw(SectionDescriptionSmall)`mx-auto text-left text-gray-300`;

const FaqsContainer = tw.div`mt-10 sm:mt-16 w-full flex-1 lg:flex justify-between items-start max-w-screen-lg mx-auto`;
const FaqsColumn = tw.div`w-full lg:max-w-lg lg:mr-12 last:mr-0`;
const Faq = tw.div`select-none cursor-pointer border-b-2 border-primary-400 hover:border-primary-500 transition-colors duration-300 py-6`;
const Question = tw.div`flex justify-between items-center`;
const QuestionText = tw.div`text-sm sm:text-lg font-semibold tracking-wide`;
const QuestionToggleIcon = styled(motion.span)`
  ${tw`ml-2 transition duration-300`}
  svg {
    ${tw`w-6 h-6`}
  }
`;
const Answer = tw(motion.div)`hidden text-sm font-normal mt-4 text-gray-300`;

export default ({
  subheading = "Community developed ",
  heading = "Buy & sell methods",
  description = `Gunbot offers 15 different methods for executing orders. All methods can be freely combined in a custom strategy, for example you can setup Gunbot to purchase an asset at a percentage from the lower Bollinger Band, and sell that asset with a trailing stop / stop loss method.

  Methods are the main trigger for an order. Additionally you can use a set of confirming indicators to specify the conditions you want to allow trading for. Every method can use a configurable stop limit to reduce your risk exposure.
  
  A wide range of candlestick periods can be used. Select methods allow for pyramid buying.
  `,
  faqs = [
    {
      question: "Average Directional Index (ADX)",
      answer:
        "Define a minimum ADX level that needs to be reached before trading."
    },
    {
      question: "Average True Range (ATR) Trailing Stop",
      answer:
        "A method using the ATR indicator to provide a configurable trailing stop."
    },
    {
      question: "Bollinger Bands",
      answer:
        "Trade when price reaches a configurable percentage from the lower or upper Bollinger Bands."
    },
    {
      question: "Bollinger Bands - crossover (BBTA) ",
      answer:
        "Trade when the spread between slow and fast EMA increases / decreases. The minimum required spread is configurable."
    },
    {
      question: "Emotionless",
      answer:
        '"Just works" method, hardy any configurable parameters. Perfect for novice traders.'
    },
    {
      question: "Gain",
      answer:
        "Buy at a configurable percentage below EMA, sell when a set percentage above the break-even point is reached. ",
    },
    {
      question: "MACD",
      answer:
        "Trade on crossing of the MACD line and the signal line.",
    },
    {
      question: "MACDH",
      answer:
        "Trade on crossing of the MACD-Histogram and the zero line.",
    },
    {
      question: "Pingpong",
      answer:
        "Buy and sell at exact configured prices.",
    },
    {
      question: "Stepgain",
      answer:
        "Using a built-in trend watcher, trade when a downtrend turns into an uptrend and vice versa.",
    },
    {
      question: "SMA cross",
      answer:
        "Trade on crossings of the slow and fast simple moving averages.",
    },
    {
      question: "Time series analysis",
      answer:
        "Attempts to predict the next period close price, trades when the forecast is higher/lower than the current price.",
    },
    {
      question: "Trailing stop / stop limit (TSSL)",
      answer:
        "Using configurable trailing ranges for buying and selling, prices are trailed for optimal entry and exit points. Orders are placed when the trailing stop is hit.",
    },

  ]
}) => {
  const faqCol1 = [];
  const faqCol2 = [];
  const [activeQuestionIndex, setActiveQuestionIndex] = useState(null);

  const toggleQuestion = questionIndex => {
    if (activeQuestionIndex === questionIndex) setActiveQuestionIndex(null);
    else setActiveQuestionIndex(questionIndex);
  };

  faqs.map((faq, index) => {
    const renderedFaq = (
      <Faq key={index} onClick={() => toggleQuestion(index)}>
        <Question>
          <QuestionText>{faq.question}</QuestionText>
          <QuestionToggleIcon
            variants={{
              collapsed: { rotate: 0 },
              open: { rotate: -180 }
            }}
            initial="collapsed"
            animate={activeQuestionIndex === index ? "open" : "collapsed"}
            transition={{ duration: 0.02, ease: [0.04, 0.62, 0.23, 0.98] }}
          >
            <ChevronDownIcon />
          </QuestionToggleIcon>
        </Question>
        <Answer
          variants={{
            open: { opacity: 1, height: "auto", marginTop: "16px", display: "block" },
            collapsed: { opacity: 0, height: 0, marginTop: "0px", display: "none" }
          }}
          initial="collapsed"
          animate={activeQuestionIndex === index ? "open" : "collapsed"}
          transition={{ duration: 0.3, ease: [0.04, 0.62, 0.23, 0.98] }}
        >
          {faq.answer}
        </Answer>
      </Faq>
    );

    if (index % 2 === 0) faqCol1.push(renderedFaq);
    else faqCol2.push(renderedFaq);

    return null;
  });
  return (
    <PrimaryBackgroundContainer>
      <ContentWithPaddingXl>
        <HeadingContainer>
          {subheading && <Subheading>{subheading}</Subheading>}
          <Heading>{heading}</Heading>
          <Description className="display-linebreak" style={{ maxWidth: "80%" }}>{description}</Description>
        </HeadingContainer>
        <FaqsContainer>
          <FaqsColumn>{faqCol1}</FaqsColumn>
          <FaqsColumn>{faqCol2}</FaqsColumn>
        </FaqsContainer>
      </ContentWithPaddingXl>
    </PrimaryBackgroundContainer>
  );
};
