import React from 'react';
import AnimationRevealPage from "helpers/AnimationRevealPage.js";

import { Container as ContainerBase } from "components/misc/Layouts";

import tw from "twin.macro";

import { css } from "styled-components/macro"; //eslint-disable-line


const Container = tw(ContainerBase)`min-h-screen bg-primary-900 text-white font-medium flex justify-center -m-8`;
const Content = tw.div`max-w-screen-xl m-0 sm:mx-20 sm:my-16 bg-white text-gray-900 shadow sm:rounded-lg flex justify-center flex-1`;

const Heading = tw.h1`text-2xl xl:text-3xl font-extrabold`;

const ExtraInfoContainer = tw.div`sm:rounded-r-lg flex-1 flex-col bg-gray-100 text-left hidden lg:flex justify-center text-gray-700`;

export default function () {

  const redirect = function () {
    setTimeout(function () {
      window.location.replace("https://www.gunbot.com/")
    }, 5000);
  }

  return (
    <>
    <AnimationRevealPage>
      <Container>
        <Content>
          <ExtraInfoContainer>
            <Heading tw="text-4xl xl:text-5xl ml-24">Success ...</Heading>
            <p tw="ml-24 mt-4">This page will redirect to the gunbot.com home page in a few seconds</p>
            {redirect()}
           
          </ExtraInfoContainer>
        </Content>
      </Container>
    </AnimationRevealPage>
    </>
  );
}

